* {
    margin: 4px;
    margin-left: 2px;
    padding: 0;
    color: white;
}

body {
    background-color: #252525;
}

.topbar {
    background-color: #202020;
    border-color: black;
    text-shadow: 1px 1px 2px black;
}

.product {
    display: inline-block;
    background-color: #202020;
    border-color: black;
    border-radius: 8px;
    text-shadow: 1px 1px 1px black;
}

.product img {
    box-shadow: 0px 0px 5px black;
}

.payDialogContent {
    background-color: #202020;
    border-color: black;
}

.thanks {
    background-color: #912c24;
    border-color: black;
    text-shadow: 1px 1px 2px black;
}